.backdrop {
	position: fixed;
	z-index: 20;
	left: 0;
	top: 0;
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.8);
	color: black;
}

.modal {
	width: 70%;
	max-height: 80vh;
	padding: 1.6rem;
	border: 1px solid #888;
	border-radius: 15px;
	overflow: auto;
	background-color: #f0eff8;
}

.line {
	margin: 1vh 0;
}

@media (max-width: 42.5em) {
	.modal {
		width: 85%;
	}
}
