.layout {
	height: 100vh;
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.container {
	flex: 1;
	position: relative;
	overflow-y: auto;
}

.main {
	height: 100%;
	max-width: 120rem;
	margin: 0 auto 0;
	background-color: #f0eff8;
	border-radius: 8px 8px 0 0;
	box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.25);
	overflow-y: auto;
}

.btn-menu {
	position: absolute;
	height: 99%;
	width: 2rem;
	z-index: 10;
	background-color: #504a96;
	color: #fff;
	border: none;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.3s;
}

.arrow-icon {
	width: 1.6rem;
	height: 1.6rem;
}

.btn-menu:hover {
	cursor: pointer;
	background-color: #464083;
}

.rotate {
	transform: rotate(180deg);
}

@media (max-width: 68em) {
	.main {
		margin-left: 2.4rem;
		margin-right: 2.4rem;
	}
}
